import axios from 'axios'
import { variable } from '../configs/variable'

export const getRestaurants = async (id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const url  = id ? `${endpoint}/api/restaurants/${id}` : `${endpoint}/api/restaurants`

        const { data } = await axios.get(`${url}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const deleteRestaurant = async (id) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }
        
        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const { data } = await axios.delete(`${endpoint}/api/restaurants/${id}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const addRestaurant = async (restaurant, id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let reponse = null

        if (id) {
            reponse =  await axios.put(`${endpoint}/api/restaurants/${id}`, restaurant, config)
        } else {
            reponse =  await axios.post(`${endpoint}/api/restaurants`, restaurant, config)
        }

        return {status: true, data: reponse?.data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}