import axios from 'axios'
import { variable } from '../configs/variable'

export const getUsers = async (type = null, id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let url = null

        if (id) {
            url  = `${endpoint}/api/users/${id}`
        } else if (type) {
            url  = `${endpoint}/api/users?type=${type}`
        } else {
            url = `${endpoint}/api/users`
        }

        const { data } = await axios.get(`${url}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const deleteUser = async (id) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        const { data } = await axios.delete(`${endpoint}/api/users/${id}`, config)

        return {status: true, data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

export const registerUser = async (user, id = null) => {
    try {

        const config = {
            headers: {
                'Content-Type': 'application/json'
            }
        }

        const endpoint = variable.env === 'local' ? variable.endpointLocal : variable.endpointProd

        let reponse = null

        if (id) {
            reponse =  await axios.put(`${endpoint}/api/users/${id}`, user, config)
        } else {
            reponse =  await axios.post(`${endpoint}/api/users`, user, config)
        }

        return {status: true, data: reponse?.data}
    } catch (error) {
        const message = error.response && error.response.data.message
                ? error.response.data.message
                : error.message
        
        return {status: false, error: message}
    }
}

